.blog1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.03em;
  line-height: 120%;
  font-weight: 500;
  font-family: inherit;
}
.blogWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.blogWrapper,
.line,
.posts {
  align-self: stretch;
}
.posts {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-141xl);
  box-sizing: border-box;
  max-width: 100%;
}
.line {
  height: 1px;
  position: relative;
  background-color: var(--border-default);
}
.about,
.banquee {
  position: relative;
  letter-spacing: -0.03em;
}
.banquee {
  margin: 0;
  font-size: inherit;
  line-height: 100%;
  font-weight: 700;
  font-family: inherit;
}
.about {
  align-self: stretch;
  line-height: 140%;
  font-weight: 500;
}
.features,
.pricing,
.support {
  position: relative;
  line-height: 26px;
  font-weight: 500;
  display: inline-block;
  min-width: 67px;
}
.pricing,
.support {
  min-width: 53px;
}
.support {
  min-width: 62px;
}
.column,
.nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.nav {
  align-self: stretch;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-base);
  font-size: var(--text-default-1-size);
  color: var(--text-muted);
  font-family: var(--text-small);
}
.column {
  flex: 1;
  gap: var(--gap-13xl);
  min-width: 153px;
  max-width: 204px;
}
