.changelog,
.licensing,
.styleguide,
.webflow {
  position: relative;
  font-weight: 500;
  display: inline-block;
}
.webflow {
  letter-spacing: -0.03em;
  line-height: 140%;
  min-width: 72px;
}
.changelog,
.licensing,
.styleguide {
  line-height: 26px;
