.column2,
.nav2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.nav2 {
  align-self: stretch;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-base);
  font-size: var(--text-default-1-size);
  color: var(--text-muted);
  font-family: var(--text-small);
}
.column2 {
  flex: 1;
  gap: var(--gap-13xl);
  min-width: 153px;
  max-width: 204px;
}
.facebook,
.instagram,
.socialMedia,
.twitter {
  position: relative;
  font-weight: 500;
  display: inline-block;
}
.socialMedia {
  letter-spacing: -0.03em;
  line-height: 140%;
  min-width: 102px;
}
.facebook,
.instagram,
.twitter {
  line-height: 26px;
  min-width: 54px;
}
.facebook,
.instagram {
  min-width: 76px;
}
.instagram {
  min-width: 77px;
}
.nav3 {
  align-self: stretch;
  flex-direction: column;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-base);
  font-size: var(--text-default-1-size);
  color: var(--text-muted);
  font-family: var(--text-small);
}
.column3,
.grid1,
.nav3 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.column3 {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-13xl);
  min-width: 153px;
  max-width: 204px;
}
.grid1 {
  width: 961px;
  flex-direction: row;
  padding: 0;
  box-sizing: border-box;
  gap: var(--gap-29xl);
  max-width: 100%;
  font-size: var(--heading-h6-size);
  color: var(--text-default);
}
.grid,
.line1 {
  align-self: stretch;
}
.grid {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.line1 {
  height: 1px;
  position: relative;
  background-color: var(--border-default);
}
.madeBy {
  white-space: pre-wrap;
}
.pawelGola,
.webflow1 {
  color: var(--text-primary);
}
.datenschutz,
.impressum,
.madeByPawelContainer {
  position: relative;
  line-height: 160%;
  font-weight: 500;
}
.datenschutz,
.impressum {
  display: inline-block;
  min-width: 74px;
}
.datenschutz {
  min-width: 85px;
}
.subnav {
  width: 191px;
  flex-direction: row;
  justify-content: flex-start;
  gap: var(--gap-13xl);
}
.container,
.grid2,
.grid3,
.subnav {
  display: flex;
  align-items: flex-start;
}
.grid3 {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.container,
.grid2 {
  flex-direction: column;
  justify-content: flex-start;
}
.grid2 {
  align-self: stretch;
  gap: var(--gap-base);
  font-size: var(--text-small-size);
  color: var(--text-muted);
  font-family: var(--text-small);
}
.container {
  width: 1400px;
  gap: var(--gap-61xl);
  max-width: 100%;
}
.footer,
.footerContent {
  align-self: stretch;
  box-sizing: border-box;
  max-width: 100%;
}
.footerContent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.footer {
  background-color: var(--background-default);
  padding: 0 0 var(--padding-29xl);
  gap: var(--gap-77xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--text-primary);
  font-family: var(--heading-h6);
}
.blog,
.footer,
.postsParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.postsParent {
  align-self: stretch;
  max-width: 100%;
}
.blog {
  width: 100%;
  position: relative;
  background-color: var(--background-default);
  gap: var(--gap-77xl);
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: 96px;
  color: var(--text-default);
  font-family: var(--heading-h6);
}
@media screen and (max-width: 1225px) {
  .posts {
    padding-bottom: var(--padding-85xl);
    box-sizing: border-box;
  }
  .grid {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .posts {
    padding-bottom: var(--padding-49xl);
    box-sizing: border-box;
  }
  .grid1 {
    flex-wrap: wrap;
  }
  .blog,
  .footer {
    gap: var(--gap-29xl);
  }
}
@media screen and (max-width: 750px) {
  .blog1 {
    font-size: 48px;
    line-height: 69px;
  }
  .banquee {
    font-size: var(--font-size-7xl);
    line-height: 26px;
  }
  .grid1 {
    gap: var(--gap-5xl);
  }
  .grid3 {
    flex-wrap: wrap;
  }
  .container {
    gap: var(--gap-21xl);
  }
}
@media screen and (max-width: 450px) {
  .blog1 {
    font-size: 29px;
    line-height: 46px;
  }
  .posts {
    padding-bottom: var(--padding-25xl);
    box-sizing: border-box;
  }
  .banquee {
    font-size: var(--font-size-lgi);
    line-height: 19px;
  }
  .container {
    gap: var(--gap-xl);
  }
  .footer {
    padding-bottom: var(--padding-12xl);
    box-sizing: border-box;
  }
  .blog,
  .footer {
    gap: var(--gap-5xl);
  }
}
