.banquee {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.03em;
  line-height: 100%;
  font-weight: 700;
  font-family: inherit;
  white-space: nowrap;
}
.navLeft {
  width: 400px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-7xs-5) 0;
  box-sizing: border-box;
  max-width: 100%;
}
.features {
  position: relative;
  line-height: 170%;
  font-weight: 500;
  display: inline-block;
  min-width: 67px;
}
.navItem {
  border-radius: var(--br-7xs);
  background-color: var(--background-default);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-5xs) var(--padding-5xs) var(--padding-5xs)
    var(--padding-xs);
}
.compare {
  position: relative;
  line-height: 170%;
  font-weight: 500;
  display: inline-block;
  min-width: 70px;
}
.riarrowDropDownLineIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.navItem1 {
  border-radius: var(--br-7xs);
  background-color: var(--background-muted);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-5xs) var(--padding-5xs) var(--padding-5xs)
    var(--padding-xs);
}
.support {
  position: relative;
  line-height: 170%;
  font-weight: 500;
  display: inline-block;
  min-width: 62px;
}
.navItem2 {
  border-radius: var(--br-9xs);
  background-color: var(--background-default);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-5xs) var(--padding-xs);
}
.blog {
  position: relative;
  line-height: 170%;
  font-weight: 500;
  display: inline-block;
  min-width: 34px;
}
.riarrowDropDownLineIcon1 {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.navCenter,
.navItem3 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.navItem3 {
  border-radius: var(--br-7xs);
  background-color: var(--background-default);
  padding: var(--padding-5xs) var(--padding-5xs) var(--padding-5xs)
    var(--padding-xs);
}
.navCenter {
  gap: var(--gap-base);
  max-width: 100%;
  font-size: var(--text-default-1-size);
  color: var(--text-default);
  font-family: var(--text-small);
}
.button,
.login {
  position: relative;
  line-height: 100%;
  display: inline-block;
}
.login {
  letter-spacing: -0.03em;
  min-width: 45px;
  white-space: nowrap;
}
.button {
  font-size: var(--font-size-mini);
  font-weight: 500;
  font-family: var(--heading-h6);
  color: var(--background-default);
  text-align: left;
  min-width: 103px;
}
.component2,
.grid {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.component2 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-xs) var(--padding-base);
  background-color: var(--text-primary);
  border-radius: var(--br-7xs);
  justify-content: center;
  white-space: nowrap;
}
.component2:hover {
  background-color: var(--color-lightseagreen-100);
}
.grid {
  justify-content: flex-end;
  gap: var(--gap-12xl);
}
.navRight {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  padding: var(--padding-11xs) 0;
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--heading-h6-size);
}
.container,
.header {
  display: flex;
  align-items: center;
  max-width: 100%;
}
.container {
  width: 1400px;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.header {
  align-self: stretch;
  background-color: var(--background-default);
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-5xl) var(--padding-xl);
  box-sizing: border-box;
  top: 0;
  z-index: 99;
  position: sticky;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--text-primary);
  font-family: var(--heading-h6);
}
@media screen and (max-width: 1225px) {
  .navCenter {
    display: none;
  }
  .container {
    width: 987px;
  }
}
@media screen and (max-width: 750px) {
  .grid {
    display: none;
  }
  .navRight {
    width: 189px;
  }
}
