.primary {
  position: relative;
  line-height: 180%;
  font-weight: 500;
  display: inline-block;
  min-width: 26px;
}
.label {
  border-radius: var(--br-9xs);
  background-color: var(--background-primary-light);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-9xs) var(--padding-3xs);
}
.forYourStartup,
.howToStart {
  margin: 0;
}
.howToStartContainer {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--heading-h2-size);
  letter-spacing: -0.03em;
  line-height: 110%;
  font-weight: 500;
  font-family: var(--heading-h6);
  color: var(--text-default);
}
.postHeader {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.loremIpsumDolor {
  width: 600px;
  position: relative;
  font-size: var(--heading-h5-size);
  line-height: 180%;
  font-weight: 500;
  color: var(--text-default);
  display: inline-block;
  max-width: 100%;
}
.grid {
  width: 1040px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  gap: var(--gap-13xl);
  max-width: 100%;
}
.etMalesuadaFames,
.imageIcon {
  position: relative;
  max-width: 100%;
}
.imageIcon {
  width: 1000px;
  border-radius: var(--br-base);
  max-height: 100%;
  overflow: hidden;
  object-fit: cover;
}
.etMalesuadaFames {
  margin: 0;
  width: 540px;
  font-size: inherit;
  letter-spacing: -0.03em;
  line-height: 34px;
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
}
.blankLine,
.eratImperdietSed,
.loremIpsumDolor1 {
  margin: 0;
}
.loremIpsumDolorContainer {
  width: 600px;
  font-size: var(--heading-h6-size);
  line-height: 180%;
  font-family: var(--text-small);
}
.loremIpsumDolorContainer,
.nasceturRidiculusMus,
.sedVulputateOdio {
  position: relative;
  font-weight: 500;
  display: inline-block;
  max-width: 100%;
}
.nasceturRidiculusMus {
  margin: 0;
  width: 540px;
  font-size: inherit;
  letter-spacing: -0.03em;
  line-height: 34px;
  font-family: inherit;
}
.sedVulputateOdio {
  width: 600px;
  height: 384px;
  font-size: var(--heading-h6-size);
  line-height: 180%;
  font-family: var(--text-small);
}
.content {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  gap: var(--gap-11xl);
  max-width: 100%;
}
.shareArticle {
  width: 101px;
  position: relative;
  letter-spacing: -0.03em;
  line-height: 140%;
  font-weight: 500;
  display: inline-block;
  min-width: 101px;
}
.ritwitterFillIcon {
  height: 22px;
  width: 22px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.badge {
  height: 40px;
  width: 40px;
  border-radius: var(--br-81xl);
  background-color: var(--background-muted);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-4xs);
  box-sizing: border-box;
}
.rifacebookCircleFillIcon {
  height: 22px;
  width: 22px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.badge1 {
  height: 40px;
  width: 40px;
  border-radius: var(--br-81xl);
  background-color: var(--background-muted);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-4xs);
  box-sizing: border-box;
}
.rilinkedinBoxFillIcon {
  height: 22px;
  width: 22px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.badge2 {
  height: 40px;
  width: 40px;
  border-radius: var(--br-81xl);
  background-color: var(--background-muted);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-4xs);
  box-sizing: border-box;
}
.riinstagramLineIcon {
  height: 22px;
  width: 22px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.badge3,
.grid1 {
  display: flex;
  flex-direction: row;
}
.badge3 {
  height: 40px;
  width: 40px;
  border-radius: var(--br-81xl);
  background-color: var(--background-muted);
  align-items: center;
  justify-content: center;
  padding: var(--padding-4xs);
  box-sizing: border-box;
}
.grid1 {
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.container,
.postDetails,
.share {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.share {
  align-self: stretch;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  min-height: 81px;
  text-align: center;
  font-size: var(--heading-h6-size);
}
.container,
.postDetails {
  max-width: 100%;
}
.postDetails {
  align-self: stretch;
  min-height: 1173px;
  text-align: left;
  font-size: var(--heading-h4-size);
  color: var(--text-default);
  font-family: var(--heading-h6);
}
.container {
  flex: 1;
  min-height: 2228px;
  text-align: center;
  font-size: var(--font-size-smi);
  color: var(--text-primary);
  font-family: var(--text-small);
}
@media screen and (max-width: 1050px) {
  .imageIcon {
    width: calc(100% - 40px);
  }
}
@media screen and (max-width: 750px) {
  .howToStartContainer {
    font-size: var(--font-size-32xl);
    line-height: 56px;
  }
  .grid {
    gap: var(--gap-base);
  }
  .sedVulputateOdio {
    max-width: 100%;
    height: 100%;
    padding-bottom: var(--padding-11xl);
  }
}
@media screen and (max-width: 450px) {
  .howToStartContainer {
    font-size: var(--font-size-19xl);
    line-height: 42px;
  }
  .loremIpsumDolor {
    font-size: var(--text-default-1-size);
    line-height: 29px;
  }
  .etMalesuadaFames,
  .nasceturRidiculusMus {
    font-size: var(--font-size-3xl);
    line-height: 27px;
  }
}
