  gap: var(--gap-29xl);
  max-width: 100%;
  font-size: var(--heading-h6-size);
  color: var(--text-default);
}
.grid8,
.line11 {
  align-self: stretch;
}
.grid8 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.line11 {
  height: 1px;
  position: relative;
  background-color: var(--border-default);
}
.madeBy {
  white-space: pre-wrap;
}
.pawelGola,
.webflow1 {
