  justify-content: flex-start;
  gap: var(--gap-13xl);
}
.grid11 {
  align-self: stretch;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.container1,
.grid10 {
  flex-direction: column;
  justify-content: flex-start;
}
.grid10 {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  gap: var(--gap-base);
  font-size: var(--text-small-size);
  color: var(--text-muted);
  font-family: var(--text-small);
}
.container1 {
  width: 1400px;
  gap: var(--gap-61xl);
  max-width: 100%;
}
.container1,
.footer,
.footerContent,
.support {
  display: flex;
  align-items: flex-start;
}
.footerContent {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.footer,
.support {
  background-color: var(--background-default);
  flex-direction: column;
  justify-content: flex-start;
}
.footer {
  align-self: stretch;
  padding: 0 0 var(--padding-29xl);
  box-sizing: border-box;
  gap: var(--gap-77xl);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--text-primary);
  font-family: var(--heading-h6);
}
.support {
  width: 100%;
  position: relative;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1225px) {
  .cardParent {
    gap: var(--gap-45xl);
  }
  .supportInner {
    padding-bottom: var(--padding-85xl);
    box-sizing: border-box;
  }
  .grid8 {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .card1 {
    flex: 1;
  }
  .cardSecurityParent {
    gap: var(--gap-13xl);
  }
  .cardParent {
    flex-wrap: wrap;
  }
  .accountDetails,
  .frameParent {
    gap: var(--gap-13xl);
  }
  .supportInner {
    padding-bottom: var(--padding-49xl);
    box-sizing: border-box;
  }
  .grid9 {
    flex-wrap: wrap;
  }
  .footer {
    gap: var(--gap-29xl);
  }
}
@media screen and (max-width: 750px) {
  .howCanWeContainer {
    font-size: var(--heading-h3-size);
    line-height: 53px;
  }
  .grid3 {
    flex-wrap: wrap;
    min-width: 100%;
  }
  .grid1 {
    gap: var(--gap-base);
  }
  .cardSecurityParent {
    min-width: 100%;
  }
  .cardParent {
    gap: var(--gap-13xl);
  }
  .accountDetails,
  .cardParent,
  .supportInner {
    padding-bottom: var(--padding-64xl);
    box-sizing: border-box;
  }
  .supportInner {
    padding-bottom: var(--padding-25xl);
  }
  .banquee {
    font-size: var(--font-size-7xl);
    line-height: 26px;
  }
  .grid9 {
    gap: var(--gap-5xl);
  }
  .grid11 {
    flex-wrap: wrap;
  }
  .container1 {
    gap: var(--gap-21xl);
  }
}
@media screen and (max-width: 450px) {
  .support1 {
    font-size: var(--text-default-1-size);
    line-height: 19px;
  }
  .howCanWeContainer {
    font-size: var(--font-size-5xl);
    line-height: 35px;
  }
  .cards1,
  .stillHaveQuestions {
    font-size: var(--font-size-3xl);
    line-height: 27px;
  }
  .howDoI,
  .howDoI1,
  .howToOrder,
  .howToSetup,
  .myCardWill {
    font-size: var(--text-default-1-size);
    line-height: 19px;
  }
  .cardDetails,
  .cardParent,
  .cardSecurityParent {
    gap: var(--gap-base);
  }
  .account1 {
    font-size: var(--font-size-3xl);
    line-height: 27px;
  }
  .canIHave,
  .howDoI2,
  .howToUpgrade {
    font-size: var(--text-default-1-size);
    line-height: 19px;
  }
  .multipleAccounts {
    gap: var(--gap-base);
  }
  .howDoI3 {
    font-size: var(--text-default-1-size);
    line-height: 19px;
  }
  .accountDetails,
  .accountDetails1 {
    gap: var(--gap-base);
  }
  .personalDetails1 {
    font-size: var(--font-size-3xl);
    line-height: 27px;
  }
  .howCanI,
  .howDoI4,
  .howToClose,
  .whereDoI {
    font-size: var(--text-default-1-size);
    line-height: 19px;
  }
  .frameParent,
  .personalDetails2 {
    gap: var(--gap-base);
  }
  .banquee {
    font-size: var(--font-size-lgi);
    line-height: 19px;
  }
  .container1 {
    gap: var(--gap-xl);
  }
  .footer {
    gap: var(--gap-5xl);
    padding-bottom: var(--padding-12xl);
    box-sizing: border-box;
  }
}
