.button,
.relatedArticles {
  position: relative;
  letter-spacing: -0.03em;
  font-weight: 500;
  display: inline-block;
}
.relatedArticles {
  margin: 0;
  width: 540px;
  font-size: inherit;
  line-height: 120%;
  font-family: inherit;
  flex-shrink: 0;
  max-width: 100%;
}
.button {
  line-height: 100%;
  min-width: 86px;
}
.riarrowDownLineIcon {
  height: 24px;
  width: 24px;
  position: relative;
  object-fit: contain;
}
.buttonText {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  font-size: var(--heading-h6-size);
  color: var(--text-primary);
}
.container,
.grid,
.posts {
  align-self: stretch;
  max-width: 100%;
}
.grid {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.container,
.posts {
  justify-content: flex-start;
}
.posts {
  display: grid;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-14xl);
  grid-template-columns: repeat(3, minmax(283px, 1fr));
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--gap-45xl);
}
.related,
.relatedWrapper {
  display: flex;
  box-sizing: border-box;
  max-width: 100%;
}
.related {
  width: 1200px;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-141xl) 0;
}
.relatedWrapper {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  text-align: left;
  font-size: var(--heading-h3-size);
  color: var(--text-default);
  font-family: var(--heading-h6);
}
@media screen and (max-width: 1225px) {
  .container {
    gap: var(--gap-13xl);
  }
  .related {
    gap: var(--gap-72xl);
  }
}
@media screen and (max-width: 1050px) {
  .posts {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(283px, 491px));
  }
  .related {
    padding-top: var(--padding-85xl);
    padding-bottom: var(--padding-85xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .relatedArticles {
    font-size: var(--font-size-13xl);
    line-height: 38px;
  }
  .grid {
    flex-wrap: wrap;
  }
  .posts {
    grid-template-columns: minmax(283px, 1fr);
  }
  .container,
  .posts {
    gap: var(--gap-base);
  }
  .related {
    gap: var(--gap-26xl);
    padding-top: var(--padding-49xl);
    padding-bottom: var(--padding-49xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .relatedArticles {
    font-size: var(--font-size-5xl);
    line-height: 29px;
  }
  .related {
    gap: var(--gap-4xl);
  }
}
