.imageIcon {
  align-self: stretch;
  height: 378px;
  position: relative;
  border-radius: var(--br-base);
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.loremIpsumDolor,
.thingsNobodyTold {
  align-self: stretch;
  position: relative;
  font-weight: 500;
}
.thingsNobodyTold {
  margin: 0;
  font-size: inherit;
  letter-spacing: -0.03em;
  line-height: 120%;
  font-family: inherit;
}
.loremIpsumDolor {
  font-size: var(--text-default-1-size);
  line-height: 160%;
  font-family: var(--text-small);
}
.grid {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.default {
  position: relative;
  line-height: 180%;
  font-weight: 500;
  display: inline-block;
  min-width: 73px;
}
.cateogories,
.label,
.post {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.label {
  border-radius: var(--br-9xs);
  background-color: var(--background-muted);
  padding: var(--padding-9xs) var(--padding-3xs);
}
.cateogories,
.post {
  font-size: var(--font-size-smi);
  font-family: var(--text-small);
}
.post {
  width: 378px;
  flex-direction: column;
  gap: var(--gap-5xl);
  max-width: 100%;
  text-align: left;
  font-size: var(--heading-h4-size);
  color: var(--text-default);
  font-family: var(--heading-h6);
}
@media screen and (max-width: 450px) {
  .thingsNobodyTold {
    font-size: var(--font-size-3xl);
    line-height: 27px;
  }
}
